.gallery{
  padding: 40px 0;
  position: relative;
  width: 100%;
  height: 80vh;
  background-color: #F4F7F8;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
}

.gallery h1{
  font-family: 'Permanent Marker', sans-serif;
  position: absolute;
  left: 50%;
  top: 0;
  font-size: 70px;

  transform: translate(-50%, -30px);
  margin-top: 10px;
  width: 100%;
  opacity: 0;
  transition: all 0.7s ease-in;
}

.gallery h1.active{
opacity: 1;
transform: translateX(-50%);
}





.gallery-container{
  margin-top: 200px;
  /* height: 64vh; */
  height: auto;
  width: auto;
  /* width: 128vh; */
  position: absolute;
  /* max-width: 1170px; */
  display: grid;
  grid-column: 4;
  grid-row: 2;
  grid-template-columns: repeat(4, 35vh);
  row-gap: 20px;
  column-gap: 20px;
  grid-template-rows: repeat(2, 35vh);
  border-radius: 30px;
  overflow: hidden;
}

.gallery-container .gpic{

  width: 35vh;
  height: 35vh;
  padding: 0;
  margin: 0;
  overflow: hidden;
  transition: all 1.2s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
}

.gallery-container .gpic-img-container{
  transition: all 1.2s ease-in-out;
  opacity: 0; 
  width: 0;
  height: 0;
}

.gallery-container .gpic-img-container.active-pic{
  width: 100%;
  height: 100%;
  opacity: 1;
}



.gallery-container img{
  width: 100%;
  height: fit-content;
  transition: all 1.2s ease-in-out;
}

.gallery-container img:hover{
  scale: 1.15;
}
.gallery-container .gpic.featured{
  height: calc(70vh + 20px);
  width: calc(70vh + 20px);
}

.gallery-container .gpic.featured img{
  grid-column: 1 / 3;
  grid-row: 1 / 3;
  /* width: 64vh;
  height: 64vh; */
  height: calc(70vh + 20px);
  width: calc(70vh + 20px);
} 

.products{
  /* width: 100vw; */
