/* Donation Page */
#donation-popup{
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  z-index: -100;
  background-color: #000;
  opacity: 0;
}
#donation-popup.active{
  z-index: 101;
  opacity: 0.7;
}

#donation-popup-content{
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  z-index: -100;
  /* background-color: #000; */
  /* opacity: 0.7; */
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transform: scale(0.1);
  transition: all 0.3s ease-in-out;
}
#donation-popup-content.active{
  z-index: 1002;
  opacity: 1;
  transform: scale(1);
}


#gcash-qr{
  /* position: fixed; */
  /* left: 0; */
  /* top: 0; */
  height: 85vh;
  width: 35vw;
  z-index: 1002;
  background-color: #fff;
  border-radius: 20px;
  position: relative;
}

#close-popup-btn{
  position: absolute;
  right: 15px;
  top: 15px;
  background-color: red;
  /* size: 40px; */
  border-radius: 5px;
  color: #fff;
  border: none;
  padding: 7px 18px 7px 18px;
}
#close-popup-btn i{
  font-size: large;
}


.donation-cycle{
  margin-top: 20px;
  width: 100%;
  max-width: 1188px;
  border-radius: 40px;
  background-color: rgb(244, 247, 248);
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  padding-top: 56px;
  padding-bottom: 100px;
  margin-bottom: 10vh;
}

.donation-cycle-container{
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 100%;
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
}

.donation-steps{
  /* margin-top: 40px; */
    flex: 1 1 0%;
    display: flex;
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
}

.step-content{
  margin-top: 44px;
    background-color: white;
    width: 220px;
    padding: 22px 32px 15px;
    border-radius: 16px;
    box-shadow: rgb(239 243 244) 0px 34px 64px;
    position: relative;
}

.step-content::before{
  content: "";
  position: absolute;
  width: 31px;
  height: 31px;
  background-color: white;
  border-radius: 4px;
  top: -12px;
  left: 50%;
  transform: translateX(-50%) rotate(-45deg);
}

.step-content p{
  color: rgb(103, 111, 114);
    margin-top: 8px;
    margin-bottom: 20px;
    margin: auto;
    font-size: 16px;
    max-width: 170px;
}

.step-number{
  width: 60px;
  height: 60px;
  background-color: white;
  border-radius: 50%;
  font-size: 18px;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
}

.donation-head{
  margin-top: 5vh;
  margin-bottom: 15vh;
}

.gcash{
  margin-top: 5vh;
  width: 350px;
  height: 60px;
  border-radius: 30px;
  background-color: #015add;
  border: none;
}

